import {IActionModel, ActionType} from '../models/action.model';
import {MethodType} from '../models/interface.model';

/**
 * 模块查询
 */
class List {
    static $inject = ["toolbarUtils", "actionUtils"];
    static key: string = "crawlerSettingListAction";

    constructor(toolbarUtils, actionUtils) {
        let actionModel: IActionModel = {
            key: List.key,
            type: ActionType.list,
            title: "爬虫配置文件管理",
            icon: "content-save-settings",
            list: {
                columns: [
                    actionUtils.columnBuilder("<span>{{::item.key}}</span>", "KEY").toValue(),
                    actionUtils.columnBuilder("<span>{{ ::item.host }}</span>", "域名").toValue(),
                    actionUtils.columnBuilder("<span>{{ ::item.interval }}</span>", "间隔时间").toValue(),
                    actionUtils.columnBuilder(`<span>{{ ::item.downloader }}</span>`, "下载策略").toValue(),
                    actionUtils.columnBuilder(`<span>{{ ::item.description }}</span>`, "详情").toValue()
                ],
                showPagination: true,
                searchActionKey: Search.key,
                showRefreshBtn: true,
                showSearchBtn: true,
                showSearchPanel: false,
                toolbars: [],
                itemToolbars: []
            },
            itemActions: [{key: Edit.key}, {key: Delete.key}, {key: Copy.key}],
            actions: [Add.key],
            interfaces: [{
                key: "crawlerSettingList",
                method: MethodType.GET,
                address: "",
                port: null,
                path: "crawler_settings",
                jpp: {
                    set: {
                        "/total": "/count",
                        "/rows": "/rows"
                    }
                },
                isRestful: true
            }]
        };

        return actionModel;
    }
}

class AddFirst {
    static $inject = ["toolbarUtils", "actionUtils"];
    static key: string = "crawlerSettingAddFirstAction";

    constructor(toolbarUtils, actionUtils) {
        let actionModel: IActionModel = {
            key: AddFirst.key,
            type: ActionType.form,
            title: "基础设置",
            icon: "add",
            form: {
                formSchema: "crawlerAddFirstActionForm"
            }
        };

        return actionModel;
    }
}

class AddSecond {
    static $inject = ["toolbarUtils", "actionUtils"];
    static key: string = "crawlerSettingAddSecondAction";

    constructor(toolbarUtils, actionUtils) {
        let actionModel: IActionModel = {
            key: AddSecond.key,
            type: ActionType.form,
            title: "白名单设置",
            icon: "add",
            form: {
                formSchema: [{
                    key: "domainWhiteList",
                    type: "chips",
                    startEmpty: true,
                    description: "域名白名单，配置可以爬取的域名列表",
                    showHints: true,
                    htmlClass: "md-block"
                }, {
                    key: "whitePathList",
                    type: "array",
                    fieldHtmlClass: "layout-row flex",
                    startEmpty: true,
                    description: "路径白名单，配置可以爬取的路径列表",
                    showHints: true,
                    items: [{
                        type: "section",
                        htmlClass: "layout-row flex",
                        items: [{
                            key: "whitePathList[].enable",
                            type: "select",
                            titleMap: [{
                                name: "启用",
                                value: true
                            }, {
                                name: "不启用",
                                value: false
                            }]
                        }, {
                            key: "whitePathList[].regexp",
                            htmlClass: "md-block flex",
                            type: "text"
                        }, {
                            key: "whitePathList[].scope",
                            type: "text"
                        }, {
                            key: "whitePathList[].description",
                            type: "text"
                        }]
                    }]
                }]
            }
        };

        return actionModel;
    }
}

class AddThird {
    static $inject = ["toolbarUtils", "actionUtils"];
    static key: string = "crawlerSettingAddThirdAction";

    constructor(toolbarUtils, actionUtils) {
        let actionModel: IActionModel = {
            key: AddThird.key,
            type: ActionType.form,
            title: "其他设置",
            icon: "add",
            form: {
                formSchema: [{
                    key: "proxySettings",
                    type: "card",
                    items: [{
                        key: "proxySettings.useProxy",
                        type: "checkbox"
                    }, {
                        key: "proxySettings.timeout",
                        type: "number",
                        htmlClass: "md-block"
                    }, {
                        key: "proxySettings.charset",
                        type: "text",
                        htmlClass: "md-block"
                    }, {
                        key: "proxySettings.ua",
                        type: "textarea",
                        maxLength: 300,
                        htmlClass: "md-block"
                    }, {
                        key: "proxySettings.ipInfo",
                        type: "card",
                        items: [{
                            key: "proxySettings.ipInfo.host",
                            type: "text",
                            htmlClass: "md-block"
                        }, {
                            key: "proxySettings.ipInfo.port",
                            type: "number",
                            htmlClass: "md-block"
                        }]
                    }],
                    htmlClass: "md-block"
                }]
            }
        };

        return actionModel;
    }
}

class AddForth {
    static $inject = ["toolbarUtils", "actionUtils"];
    static key: string = "crawlerSettingAddForthAction";

    constructor(toolbarUtils, actionUtils) {
        let actionModel: IActionModel = {
            key: AddFifth.key,
            type: ActionType.form,
            title: "页面配置",
            icon: "add",
            form: {
                formSchema: [{
                    key: "pages",
                    type: "tabarray",
                    startEmpty: true,
                    fieldHtmlClass: "layout-column flex",
                    items: [{
                        type: "section",
                        grid: {flex: ""},
                        items: [{
                            key: "pages[].key",
                            type: "text",
                            htmlClass: "md-block"
                        }, {
                            key: "pages[].priority",
                            type: "number",
                            htmlClass: "md-block"
                        }, {
                            key: "pages[].fieldKey",
                            type: "text",
                            htmlClass: "md-block"
                        }, {
                            key: "pages[].strict",
                            type: "switch",
                            htmlClass: "md-block"
                        }, {
                            key: "pages[].strictFields",
                            startEmpty: true,
                            type: "chips",
                            htmlClass: "md-block"
                        }, {
                            key: "pages[].rule",
                            type: "array",
                            startEmpty: true,
                            items: [{
                                type: "section",
                                htmlClass: "layout-row flex",
                                items: [{
                                    key: "pages[].rule[].regexp",
                                    htmlClass: "md-block flex",
                                    type: "text"
                                }, {
                                    key: "pages[].rule[].scope",
                                    type: "text"
                                }]
                            }]
                        }]
                    }]
                }]
            }
        };

        return actionModel;
    }
}

class AddFifth {
    static $inject = ["toolbarUtils", "actionUtils"];
    static key: string = "crawlerSettingAddFifthAction";

    constructor(toolbarUtils, actionUtils) {
        let actionModel: IActionModel = {
            key: AddFifth.key,
            type: ActionType.form,
            title: "页面配置",
            icon: "add",
            form: {
                formSchema: [{
                    key: "pages",
                    type: "tabarray",
                    items: [{
                        type: "section",
                        htmlClass: "column-row flex",
                        items: [{
                            key: "pages[].key",
                            type: "text",
                            htmlClass: "md-block"
                        }, {
                            key: "pages[].areas",
                            type: "array",
                            startEmpty: true,
                            description: "区域配置，用户优化分析性能，减少dom的查询。",
                            showHints: true,
                            items: [{
                                type: "section",
                                grid: {flex: "", layout: "row"},
                                items: [{
                                    key: "pages[].areas[].key",
                                    type: "text"
                                }, {
                                    key: "pages[].areas[].selector",
                                    htmlClass: "md-block flex",
                                    type: "text"
                                }, {
                                    key: "pages[].areas[].dealStrategy",
                                    type: "text"
                                }]
                            }]
                        }, {
                            key: "pages[].fields",
                            type: "jeditor",
                            htmlClass: "md-block"
                        }]
                    }]
                }]
            }
        };

        return actionModel;
    }
}

class Add {
    static key: string = "crawlerSettingAddAction";

    constructor() {
        let actionModel: IActionModel = {
            key: Add.key,
            type: ActionType.wizard,
            title: "新建爬虫配置文件",
            icon: "add",
            refreshList: true,
            wizard: {
                defaultSchema: {
                    dataSchema: "crawlerActionData",
                },
                actions: [AddFirst.key, AddSecond.key, AddThird.key, AddForth.key, AddFifth.key]
            },
            closeDialog: true,
            interfaces: [{
                key: "crawlerSettingAdd",
                method: MethodType.POST,
                address: "",
                port: null,
                path: "crawler_settings",
                isRestful: true
            }]
        };

        return actionModel;
    }
}

class Copy {
    static key: string = "crawlerSettingCopyAction";

    constructor() {
        let actionModel: IActionModel = {
            key: Copy.key,
            type: ActionType.wizard,
            title: "复制爬虫配置文件",
            icon: "content_copy",
            refreshList: true,
            wizard: {
                defaultSchema: {
                    dataSchema: "crawlerActionData",
                },
                actions: [AddFirst.key, AddSecond.key, AddThird.key, AddForth.key, AddFifth.key]
            },
            closeDialog: true,
            interfaces: [{
                key: "crawlerSettingAdd",
                method: MethodType.POST,
                address: "",
                port: null,
                path: "crawler_settings",
                isRestful: true
            }]
        };

        return actionModel;
    }
}

class Edit {
    static $inject = ["toolbarUtils", "actionUtils"];
    static key: string = "crawlerSettingEditAction";

    constructor(toolbarUtils, actionUtils) {
        let actionModel: IActionModel = {
            key: Edit.key,
            type: ActionType.wizard,
            title: "修改爬虫配置文件",
            icon: "edit",
            refreshList: true,
            wizard: {
                defaultSchema: {
                    dataSchema: "crawlerActionData",
                },
                actions: [AddFirst.key, AddSecond.key, AddThird.key, AddForth.key, AddFifth.key]
            },
            closeDialog: true,
            interfaces: [{
                key: "crawlerSettingEdit",
                method: MethodType.PUT,
                idFieldPath: "/key",
                address: "",
                port: null,
                path: "crawler_settings",
                isRestful: true
            }]
        };

        return actionModel;
    }
}

class Delete {
    static key: string = "crawlerSettingDeleteAction";

    constructor() {
        let actionModel: IActionModel = {
            key: Delete.key,
            type: ActionType.confirm,
            title: "删除爬虫配置文件",
            icon: "delete",
            refreshList: true,
            confirm: {
                confirmTitle: "",
                confirmContent: "确定要删除爬虫配置文件吗!"
            },
            interfaces: [{
                key: "crawlerSettingDelete",
                method: MethodType.DELETE,
                idFieldPath: "/key",
                address: "",
                port: null,
                path: "crawler_settings",
                isRestful: true
            }]
        };

        return actionModel;
    }
}

class Search {
    static key: string = "crawlerSettingSearchAction";

    constructor() {
        let actionModel: IActionModel = {
            key: Search.key,
            type: ActionType.form,
            title: "搜索爬虫配置文件",
            icon: "search",
            form: {
                dataSchema: "crawlerActionData",
                formSchema: [{
                    key: "key",
                    type: "text",
                    required: false,
                    copyValueTo: ["/key/$eq"],
                    htmlClass: "md-block"
                }]
            }
        };

        return actionModel;
    }
}

class Ack {
    static key: string = "crawlerSettingAckAction";

    constructor() {
        let actionModel: IActionModel = {
            key: Ack.key,
            type: ActionType.form,
            title: "爬虫命令",
            icon: "apple-keyboard-command",
            form: {
                dataSchema: "crawlerAckActionData",
                formSchema: "crawlerAckActionForm"
            }
        };

        return actionModel;
    }
}

export default (module: ng.IModule) => {
    const services: Array<any> = [List, Search, Add, Edit, Delete, Copy, Ack, AddFirst, AddSecond, AddThird, AddForth, AddFifth];

    _.each(services, (ser)=> {
        module.service(ser.key, ser);
    });
}



